import uniIcons from "@/components/uni-icons/uni-icons.vue"
import {
	mapGetters,
	mapMutations
} from 'vuex'
export default {
	computed: {
		...mapGetters(['shopcar', 'selectShop']),
		price() {
			let price = 0;
			this.selectShop.forEach(item => {
				price += Number(item.price * item.count)
			})
			this.SET_PRICE(price)
			return price * 100
		}
	},
	watch: {
		shopcar: {
			handler() {
				let sign = true
				let selectshop = []
				this.shopcar.forEach((item => {
					if (item.checked == false) {
						this.selectAll = false
						sign = false
						return
					} else {
						selectshop.push(item)
					}
				}))
				this.SET_SELECTSHOP(selectshop)
				if (sign) {
					this.selectAll = true
				}
			},
			deep: true
		},

	},
	data() {
		return {
			selectAll: false
		}
	},
	methods: {
		onChange() {
			let sign = false
			let list = this.shopcar
			list.forEach((item => {
				if (item.checked == false) {
					sign = true
					return
				}
			}))
			if (sign) {
				list.forEach((item => {
					item.checked = true
				}))
			} else {
				list.forEach((item => {
					item.checked = false
				}))
			}
			this.SET_SHOPCAR(list)
			this.selectAll = !this.selectAll
		},
		selectIcon(index) {
			let list = this.shopcar
			this.$set(list[index], 'checked', !list[index].checked)
			this.SET_SHOPCAR(list)
		},
		selectShopItem(index) {
			uni.navigateTo({
				url: `/pages/detail/detail?id=${this.shopcar[index].id}`
			})
		},
		onSubmit() {
			if (!this.selectShop.length) {
				uni.showToast({
					icon: 'none',
					title: '购物车为空！',
					duration: 2000
				});
			} else {
				uni.showToast({
					icon: 'none',
					title: '暂未实现！',
					duration: 2000
				});
			}
		},
		...mapMutations({
			'SET_SHOPCAR': 'SET_SHOPCAR',
			'SET_PRICE': 'SET_PRICE',
			'SET_SELECTSHOP': 'SET_SELECTSHOP'
		})
	},
	components: {
		uniIcons
	}
}
